@mixin font($size, $height: $size, $weight: bold, $style: normal) {
    font-size: $size;
    line-height: $height;
    font-weight: $weight;
    font-style: $style;
}

@mixin columns ($width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    >* {
        flex-basis:$width ;
    }
}

@mixin media-laptop {
    @media (max-width: $content-width) {
        @content;
    } 
}

@mixin media-table {
    @media (max-width: $table-width) {
        @content;
    } 
}

@mixin media-mobile {
    @media (max-width: $mobile-big-width) {
        @content;
    } 
}

@mixin media-mobilesmall {
    @media (max-width: $mobile-small-width) {
        @content;
    } 
}